//Base URL
const key = process.env.REACT_APP_RAWG_API;
const base_url = `https://api.rawg.io/api/games?key=${key}`;

//Current month
const getCurrentMonth = () => {
  const month = new Date().getMonth() + 1;
  if (month < 10) {
    return `0${month}`;
  } else {
    return month;
  }
};

//Current day
const getCurrentDay = () => {
  const day = new Date().getDate();
  if (day < 10) {
    return `0${day}`;
  } else {
    return day;
  }
};

//Current day/month/year
const currentYear = new Date().getFullYear();
const currentMonth = getCurrentMonth();
const currentDay = getCurrentDay();
const currentDate = `${currentYear}-${currentMonth}-${currentDay}`;
const lastYear = `${currentYear - 1}-${currentMonth}-${currentDay}`;
const nextYear = `${currentYear + 1}-${currentMonth}-${currentDay}`;

// Const popular games
const popular_games = `&dates=${lastYear},${currentDate}&ordering=-rating&page_size=6`;
const upcoming_games = `&dates=${currentDate},${nextYear}&ordering=-added&page_size=6`;
const newGames = `&dates=${lastYear},${currentDate}&ordering=-released&page_size=6`;

export const popularGamesURL = () => `${base_url}${popular_games}`;
export const upcomingGamesURL = () => `${base_url}${upcoming_games}`;
export const newGamesURL = () => `${base_url}${newGames}`;

//Game details
export const gameDetailsURL = (game_id) =>
  `https://api.rawg.io/api/games/${game_id}?key=${key}`;
//Game screenshots
export const gameScreenshotURL = (game_id) =>
  `https://api.rawg.io/api/games/${game_id}/screenshots?key=${key}`;

//Searched game
export const searchGameURL = (game_name) =>
  `${base_url}&search=${game_name}&page_size=6 `;
